'use client';

import { Activity, Users, CreditCard, BarChart } from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { StatsCard } from '@/components/ui/StatsCard';
import { UsageChart } from '@/components/charts/UsageChart';

export default function DashboardPage() {
    const stats = {
        totalRequests: '1.2M',
        activeClients: 42,
        revenue: '$24,580',
        pendingInvoices: 7
    };

    const recentClients = [
        { id: '1', name: 'Acme Corp', usage: 45000, status: 'active' },
        { id: '2', name: 'Globex Inc', usage: 32000, status: 'active' },
        { id: '3', name: 'Stark Industries', usage: 28000, status: 'active' },
        { id: '4', name: 'Wayne Enterprises', usage: 15000, status: 'inactive' },
    ];

    return (
        <div className="space-y-6 animate-fade-in">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
                    <p className="text-gray-600 mt-2">Monitor your USSD service performance and client activity</p>
                </div>
                <div className="text-sm text-gray-500">
                    Last updated: Today, 10:30 AM
                </div>
            </div>

            {/* Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <StatsCard
                    title="Total USSD Requests"
                    value={stats.totalRequests}
                    icon={<Activity className="h-6 w-6 text-primary" />}
                    trend={{ value: 12.5, isPositive: true }}
                />
                <StatsCard
                    title="Active Clients"
                    value={stats.activeClients}
                    icon={<Users className="h-6 w-6 text-blue-600" />}
                    trend={{ value: 5.2, isPositive: true }}
                />
                <StatsCard
                    title="Revenue"
                    value={stats.revenue}
                    icon={<CreditCard className="h-6 w-6 text-green-600" />}
                    trend={{ value: 8.7, isPositive: true }}
                />
                <StatsCard
                    title="Pending Invoices"
                    value={stats.pendingInvoices}
                    icon={<BarChart className="h-6 w-6 text-amber-600" />}
                    trend={{ value: 3.2, isPositive: false }}
                />
            </div>

            {/* Chart Section */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                <Card className="lg:col-span-2">
                    <div className="flex items-center justify-between mb-6">
                        <h2 className="text-xl font-semibold text-gray-900">Usage Overview</h2>
                        <select className="text-sm border border-gray-200 rounded-lg px-3 py-1.5 focus:outline-none focus:ring-2 focus:ring-primary/20">
                            <option>Last 7 days</option>
                            <option>Last 30 days</option>
                            <option>Last quarter</option>
                        </select>
                    </div>
                    <UsageChart />
                </Card>

                {/* Recent Clients */}
                <Card>
                    <h2 className="text-xl font-semibold text-gray-900 mb-6">Recent Clients</h2>
                    <div className="space-y-4">
                        {recentClients.map((client) => (
                            <div key={client.id} className="flex items-center justify-between p-3 hover:bg-gray-50 rounded-xl transition-colors">
                                <div>
                                    <p className="font-medium text-gray-900">{client.name}</p>
                                    <p className="text-sm text-gray-500">{client.usage.toLocaleString()} requests</p>
                                </div>
                                <span className={`px-3 py-1 rounded-full text-xs font-medium ${client.status === 'active'
                                        ? 'bg-green-100 text-green-800'
                                        : 'bg-gray-100 text-gray-800'
                                    }`}>
                                    {client.status}
                                </span>
                            </div>
                        ))}
                    </div>
                    <button className="w-full mt-6 py-2.5 text-sm font-medium text-primary hover:bg-primary/5 rounded-xl transition-colors">
                        View all clients â†’
                    </button>
                </Card>
            </div>

            {/* Quick Actions */}
            <Card>
                <h2 className="text-xl font-semibold text-gray-900 mb-6">Quick Actions</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <button className="p-4 border-2 border-dashed border-gray-200 rounded-xl hover:border-primary hover:bg-primary/5 transition-all text-center">
                        <div className="text-2xl mb-2">+</div>
                        <p className="font-medium text-gray-900">Add New Client</p>
                        <p className="text-sm text-gray-500 mt-1">Create client account</p>
                    </button>
                    <button className="p-4 border-2 border-dashed border-gray-200 rounded-xl hover:border-primary hover:bg-primary/5 transition-all text-center">
                        <div className="text-2xl mb-2">ðŸ“Š</div>
                        <p className="font-medium text-gray-900">Generate Report</p>
                        <p className="text-sm text-gray-500 mt-1">Export usage data</p>
                    </button>
                    <button className="p-4 border-2 border-dashed border-gray-200 rounded-xl hover:border-primary hover:bg-primary/5 transition-all text-center">
                        <div className="text-2xl mb-2">âš¡</div>
                        <p className="font-medium text-gray-900">Top Up Credits</p>
                        <p className="text-sm text-gray-500 mt-1">Add service credits</p>
                    </button>
                </div>
            </Card>
        </div>
    );
}